 */

 .wp-block-apppresser-hero-slider {
	position: relative;

	.hero-slider {
		position: relative;
		overflow: hidden;
	}
	
	.hero-slider .hero-slider-image {
		position: absolute;
		left: 0;
		height: 100%;
		width: 100%;
		object-fit: cover;
		background-size: cover;
		background-position: center;
	}
	
	.hero-slider-overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	
	.hero-slider-inner-blocks {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	
}



@keyframes imgFade {
    0%, 20% {
        opacity: 1; // Image is fully visible
    }
    25%, 100% {
        opacity: 0; // Image starts to fade out
    }
}
